'use client'

// ==========================================
// RISIVO CRM - Theme Management Hook
// ==========================================

import { useState, useEffect, useCallback, useMemo } from 'react'
import { 
  SubAccountTheme, 
  ThemePreset,
  ThemeCSSVariables,
  RISIVO_DEFAULT_THEME,
  themeToCSSVariables,
  applyThemeToDocument,
} from '@/lib/theme'

interface UseThemeOptions {
  subAccountId?: string
  autoApply?: boolean  // Automatically apply theme to document
  onThemeChange?: (theme: SubAccountTheme) => void
  onError?: (error: Error) => void
}

interface UseThemeReturn {
  // Current theme state
  theme: SubAccountTheme
  cssVariables: ThemeCSSVariables
  isLoading: boolean
  error: string | null
  
  // Theme presets
  presets: ThemePreset[]
  presetsLoading: boolean
  
  // Actions
  loadTheme: (subAccountId: string) => Promise<void>
  saveTheme: (updates: Partial<SubAccountTheme>) => Promise<void>
  applyPreset: (presetName: string) => Promise<void>
  extractColorsFromLogo: (logoUrl: string) => Promise<Partial<SubAccountTheme>>
  resetTheme: () => Promise<void>
  
  // Utilities
  updateColor: (key: keyof SubAccountTheme, value: string) => void
  applyToDocument: () => void
}

export function useTheme(options: UseThemeOptions = {}): UseThemeReturn {
  const { 
    subAccountId: initialSubAccountId, 
    autoApply = true,
    onThemeChange,
    onError,
  } = options
  
  // State
  const [theme, setTheme] = useState<SubAccountTheme>(RISIVO_DEFAULT_THEME)
  const [presets, setPresets] = useState<ThemePreset[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const [presetsLoading, setPresetsLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [currentSubAccountId, setCurrentSubAccountId] = useState<string | undefined>(initialSubAccountId)
  
  // Compute CSS variables
  const cssVariables = useMemo(() => themeToCSSVariables(theme), [theme])
  
  // Apply theme to document
  const applyToDocument = useCallback(() => {
    applyThemeToDocument(theme)
  }, [theme])
  
  // Auto-apply when theme changes
  useEffect(() => {
    if (autoApply && typeof window !== 'undefined') {
      applyToDocument()
      // Signal that theme has been applied (for loader hiding)
      ;(window as any).__RISIVO_THEME_APPLIED__ = true
    }
    onThemeChange?.(theme)
  }, [theme, autoApply, applyToDocument, onThemeChange])
  
  // Load theme from API
  const loadTheme = useCallback(async (subAccountId: string) => {
    setIsLoading(true)
    setError(null)
    setCurrentSubAccountId(subAccountId)
    
    try {
      const response = await fetch(`/api/v1/themes?subAccountId=${subAccountId}`)
      const data = await response.json()
      
      if (!data.success) {
        throw new Error(data.error || 'Failed to load theme')
      }
      
      setTheme(data.data)
    } catch (err) {
      const message = err instanceof Error ? err.message : 'Failed to load theme'
      setError(message)
      onError?.(err instanceof Error ? err : new Error(message))
    } finally {
      setIsLoading(false)
    }
  }, [onError])
  
  // Load presets
  const loadPresets = useCallback(async () => {
    setPresetsLoading(true)
    
    try {
      const response = await fetch('/api/v1/themes?presets=true')
      const data = await response.json()
      
      if (data.success) {
        setPresets(data.data)
      }
    } catch (err) {
      console.error('Failed to load presets:', err)
    } finally {
      setPresetsLoading(false)
    }
  }, [])
  
  // Save theme to API
  const saveTheme = useCallback(async (updates: Partial<SubAccountTheme>) => {
    if (!currentSubAccountId) {
      throw new Error('No sub-account selected')
    }
    
    setIsLoading(true)
    setError(null)
    
    try {
      const response = await fetch('/api/v1/themes', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          subAccountId: currentSubAccountId,
          theme: updates,
        }),
      })
      
      const data = await response.json()
      
      if (!data.success) {
        throw new Error(data.error || 'Failed to save theme')
      }
      
      setTheme(data.data)
    } catch (err) {
      const message = err instanceof Error ? err.message : 'Failed to save theme'
      setError(message)
      onError?.(err instanceof Error ? err : new Error(message))
      throw err
    } finally {
      setIsLoading(false)
    }
  }, [currentSubAccountId, onError])
  
  // Apply preset
  const applyPreset = useCallback(async (presetName: string) => {
    if (!currentSubAccountId) {
      throw new Error('No sub-account selected')
    }
    
    setIsLoading(true)
    setError(null)
    
    try {
      const response = await fetch('/api/v1/themes', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          subAccountId: currentSubAccountId,
          applyPreset: presetName,
        }),
      })
      
      const data = await response.json()
      
      if (!data.success) {
        throw new Error(data.error || 'Failed to apply preset')
      }
      
      setTheme(data.data)
    } catch (err) {
      const message = err instanceof Error ? err.message : 'Failed to apply preset'
      setError(message)
      onError?.(err instanceof Error ? err : new Error(message))
      throw err
    } finally {
      setIsLoading(false)
    }
  }, [currentSubAccountId, onError])
  
  // Extract colors from logo
  const extractColorsFromLogo = useCallback(async (logoUrl: string): Promise<Partial<SubAccountTheme>> => {
    setIsLoading(true)
    setError(null)
    
    try {
      const response = await fetch('/api/v1/themes/extract-colors', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ logoUrl }),
      })
      
      const data = await response.json()
      
      if (!data.success) {
        throw new Error(data.error || 'Failed to extract colors')
      }
      
      return data.data.suggestedTheme
    } catch (err) {
      const message = err instanceof Error ? err.message : 'Failed to extract colors'
      setError(message)
      onError?.(err instanceof Error ? err : new Error(message))
      throw err
    } finally {
      setIsLoading(false)
    }
  }, [onError])
  
  // Reset theme to default
  const resetTheme = useCallback(async () => {
    if (!currentSubAccountId) {
      setTheme(RISIVO_DEFAULT_THEME)
      return
    }
    
    setIsLoading(true)
    setError(null)
    
    try {
      const response = await fetch(`/api/v1/themes?subAccountId=${currentSubAccountId}`, {
        method: 'DELETE',
      })
      
      const data = await response.json()
      
      if (!data.success) {
        throw new Error(data.error || 'Failed to reset theme')
      }
      
      setTheme(data.data)
    } catch (err) {
      const message = err instanceof Error ? err.message : 'Failed to reset theme'
      setError(message)
      onError?.(err instanceof Error ? err : new Error(message))
      throw err
    } finally {
      setIsLoading(false)
    }
  }, [currentSubAccountId, onError])
  
  // Update single color (local state only, call saveTheme to persist)
  const updateColor = useCallback((key: keyof SubAccountTheme, value: string) => {
    setTheme(prev => ({
      ...prev,
      [key]: value,
    }))
  }, [])
  
  // Initial load
  useEffect(() => {
    loadPresets()
    
    if (initialSubAccountId) {
      loadTheme(initialSubAccountId)
    }
  }, [initialSubAccountId, loadPresets, loadTheme])
  
  return {
    // State
    theme,
    cssVariables,
    isLoading,
    error,
    presets,
    presetsLoading,
    
    // Actions
    loadTheme,
    saveTheme,
    applyPreset,
    extractColorsFromLogo,
    resetTheme,
    
    // Utilities
    updateColor,
    applyToDocument,
  }
}

export default useTheme
