// ==========================================
// RISIVO CRM - Theme System Type Definitions
// ==========================================

/**
 * Complete theme configuration for a sub-account
 * All colors follow CSS color format (hex, rgb, hsl)
 */
export interface SubAccountTheme {
  id?: string
  subAccountId: string
  
  // Logo
  logoUrl?: string | null
  logoWidth?: number
  logoHeight?: number
  
  // Favicon (browser tab icon)
  faviconUrl?: string | null
  
  // Primary Colors (Sidebar, Main Actions)
  primaryColor: string          // Main brand color
  primaryHover: string          // Primary color hover state
  primaryForeground: string     // Text on primary background
  primaryAccent?: string        // Lighter variant of primary
  
  // Secondary Colors (Accents, Highlights)
  secondaryColor: string        // Secondary brand color (buttons)
  secondaryHover: string        // Secondary hover state
  secondaryForeground: string   // Text on secondary background
  
  // Background Colors
  backgroundColor: string       // Main page background
  surfaceColor: string          // Cards, modals background
  surfaceHover: string          // Surface hover state
  
  // Text Colors
  textPrimary: string           // Main text color
  textSecondary: string         // Muted text color
  textMuted: string             // Disabled/placeholder text
  
  // Border Colors
  borderColor: string           // Default border color
  borderHover: string           // Border hover state
  
  // Status Colors
  successColor: string
  warningColor: string
  errorColor: string
  infoColor: string
  
  // Sidebar-specific
  sidebarBg?: string
  sidebarText?: string
  sidebarTextMuted?: string
  sidebarHover?: string
  sidebarActive: string
  sidebarActiveForeground: string
  
  // Button-specific
  buttonPrimaryBg?: string
  buttonPrimaryText: string
  buttonSecondaryBg: string
  buttonSecondaryText?: string
  
  // Header/Navigation
  headerBg: string
  headerText: string
  headerBorder: string
  
  // Input fields
  inputBg: string
  inputBorder: string
  inputFocus?: string
  inputText: string
  inputPlaceholder: string
  
  // Modal/Overlay
  modalBg: string
  overlayBg: string
  
  // Shadow
  shadowColor: string
  
  // Border radius
  borderRadiusSmall: string
  borderRadiusMedium: string
  borderRadiusLarge: string
  borderRadiusFull: string
  
  // Typography
  fontFamily: string
  fontSizeBase: string
  fontWeightNormal: string
  fontWeightMedium: string
  fontWeightBold: string
  
  // Meta
  colorSource: 'manual' | 'logo_extracted' | 'preset'
  presetName?: string | null
  
  // Brand Voice (for AI content generation)
  brandVoiceToneName?: string | null
  brandVoiceTargetAudience?: string | null
  brandVoicePersonality?: string | null
  brandVoiceWritingStyle?: string | null
  brandVoiceKeyPhrases?: string | null
  brandVoiceAvoidPhrases?: string | null
  brandVoiceSampleCopy?: string | null
  
  createdAt?: string
  updatedAt?: string
}

/**
 * Theme preset for quick selection
 */
export interface ThemePreset {
  id: string
  name: string
  displayName: string
  description?: string
  category: 'general' | 'industry' | 'seasonal'
  thumbnail?: string
  
  // Core colors
  primaryColor: string
  primaryHover?: string
  secondaryColor: string
  secondaryHover?: string
  backgroundColor?: string
  surfaceColor?: string
  textPrimary?: string
  textSecondary?: string
  
  // Status colors
  successColor?: string
  warningColor?: string
  errorColor?: string
  infoColor?: string
  
  isDefault: boolean
  isActive: boolean
  displayOrder: number
}

/**
 * CSS variables mapping for theme injection
 */
export interface ThemeCSSVariables {
  // Primary
  '--theme-primary': string
  '--theme-primary-hover': string
  '--theme-primary-foreground': string
  '--theme-primary-accent': string
  
  // Secondary
  '--theme-secondary': string
  '--theme-secondary-hover': string
  '--theme-secondary-foreground': string
  
  // Background
  '--theme-background': string
  '--theme-surface': string
  '--theme-surface-hover': string
  
  // Text
  '--theme-text-primary': string
  '--theme-text-secondary': string
  '--theme-text-muted': string
  
  // Border
  '--theme-border': string
  '--theme-border-hover': string
  
  // Status
  '--theme-success': string
  '--theme-warning': string
  '--theme-error': string
  '--theme-info': string
  
  // Sidebar
  '--theme-sidebar-bg': string
  '--theme-sidebar-text': string
  '--theme-sidebar-hover': string
  '--theme-sidebar-active': string
  '--theme-sidebar-active-foreground': string
  
  // Button
  '--theme-button-primary-bg': string
  '--theme-button-primary-text': string
  '--theme-button-secondary-bg': string
  '--theme-button-secondary-text': string
  
  // Header
  '--theme-header-bg': string
  '--theme-header-text': string
  '--theme-header-border': string
  
  // Input
  '--theme-input-bg': string
  '--theme-input-border': string
  '--theme-input-focus': string
  '--theme-input-text': string
  '--theme-input-placeholder': string
  
  // Modal
  '--theme-modal-bg': string
  '--theme-overlay-bg': string
  
  // Shadow
  '--theme-shadow': string
  
  // Radius
  '--theme-radius-sm': string
  '--theme-radius-md': string
  '--theme-radius-lg': string
  '--theme-radius-full': string
  
  // Typography
  '--theme-font-family': string
  '--theme-font-size-base': string
  '--theme-font-normal': string
  '--theme-font-medium': string
  '--theme-font-bold': string
}

/**
 * Extracted colors from logo
 */
export interface ExtractedLogoColors {
  dominant: string
  palette: string[]
  vibrant?: string
  muted?: string
  darkVibrant?: string
  darkMuted?: string
  lightVibrant?: string
  lightMuted?: string
}

/**
 * Theme update request
 */
export interface ThemeUpdateRequest {
  subAccountId: string
  theme: Partial<SubAccountTheme>
  applyPreset?: string  // Preset name to apply
  extractFromLogo?: string  // Logo URL to extract colors from
}

/**
 * API Response types
 */
export interface ThemeApiResponse<T = SubAccountTheme> {
  success: boolean
  data?: T
  error?: string
  message?: string
}

export interface ThemePresetsResponse {
  success: boolean
  data: ThemePreset[]
  error?: string
}
