'use client';

import React from 'react';

export interface Message {
  id: string;
  role: 'user' | 'assistant';
  content: string;
  timestamp: Date | string;
  functionCalls?: FunctionCall[];
}

export interface FunctionCall {
  name: string;
  arguments: any;
  result?: any;
  status: 'pending' | 'success' | 'error';
}

interface MessageBubbleProps {
  message: Message;
}

export function MessageBubble({ message }: MessageBubbleProps) {
  const isUser = message.role === 'user';
  const timestamp = typeof message.timestamp === 'string' 
    ? new Date(message.timestamp) 
    : message.timestamp;

  return (
    <div className={`flex ${isUser ? 'justify-end' : 'justify-start'} mb-4`}>
      <div className={`flex ${isUser ? 'flex-row-reverse' : 'flex-row'} items-start gap-3 max-w-[80%]`}>
        {/* Avatar */}
        <div className={`flex-shrink-0 w-8 h-8 rounded-full flex items-center justify-center text-white font-semibold ${
          isUser ? 'bg-blue-600' : 'bg-gradient-to-br from-purple-600 to-pink-600'
        }`}>
          {isUser ? 'üë§' : 'ü§ñ'}
        </div>

        {/* Message Content */}
        <div className="flex flex-col gap-2">
          {/* Message Bubble */}
          <div className={`rounded-2xl px-4 py-3 ${
            isUser 
              ? 'bg-blue-600 text-white' 
              : 'bg-gray-100 text-gray-900 border border-gray-200'
          }`}>
            <p className="text-sm leading-relaxed whitespace-pre-wrap">{message.content}</p>
          </div>

          {/* Function Calls */}
          {message.functionCalls && message.functionCalls.length > 0 && (
            <div className="space-y-2">
              {message.functionCalls.map((func, index) => (
                <FunctionCallDisplay key={index} functionCall={func} />
              ))}
            </div>
          )}

          {/* Timestamp */}
          <span className={`text-xs text-gray-500 ${isUser ? 'text-right' : 'text-left'}`}>
            {timestamp.toLocaleTimeString('en-US', { hour: 'numeric', minute: '2-digit', hour12: true })}
          </span>
        </div>
      </div>
    </div>
  );
}

function FunctionCallDisplay({ functionCall }: { functionCall: FunctionCall }) {
  const statusIcons = {
    pending: '‚è≥',
    success: '‚úÖ',
    error: '‚ùå',
  };

  const statusColors = {
    pending: 'bg-yellow-50 border-yellow-200 text-yellow-800',
    success: 'bg-green-50 border-green-200 text-green-800',
    error: 'bg-red-50 border-red-200 text-red-800',
  };

  return (
    <div className={`rounded-lg border p-3 text-xs ${statusColors[functionCall.status]}`}>
      <div className="flex items-center gap-2 mb-1">
        <span>{statusIcons[functionCall.status]}</span>
        <span className="font-semibold">Function: {functionCall.name}()</span>
      </div>
      
      {functionCall.result && (
        <div className="mt-2 font-mono text-xs opacity-80">
          <pre className="whitespace-pre-wrap">
            {typeof functionCall.result === 'string' 
              ? functionCall.result 
              : JSON.stringify(functionCall.result, null, 2)
            }
          </pre>
        </div>
      )}
    </div>
  );
}
