'use client';

import { useState, useCallback, useRef, useEffect } from 'react';
import { Message } from '@/components/ai/MessageBubble';

interface UseAiChatOptions {
  subAccountId: string;
  userId: string;
  conversationId?: string;
  onError?: (error: Error) => void;
}

interface UseAiChatReturn {
  messages: Message[];
  isLoading: boolean;
  error: string | null;
  sendMessage: (content: string) => Promise<void>;
  clearMessages: () => void;
  loadConversation: (conversationId: string) => Promise<void>;
}

export function useAiChat({ 
  subAccountId,
  userId,
  conversationId: initialConversationId,
  onError 
}: UseAiChatOptions): UseAiChatReturn {
  const [messages, setMessages] = useState<Message[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [conversationId, setConversationId] = useState<string | undefined>(initialConversationId);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when new messages arrive
  const scrollToBottom = useCallback(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, []);

  useEffect(() => {
    scrollToBottom();
  }, [messages, scrollToBottom]);

  // Load conversation history
  const loadConversation = useCallback(async (convId: string) => {
    try {
      setIsLoading(true);
      setError(null);

      const response = await fetch(`/api/v1/ai/chat?conversationId=${convId}`);
      
      if (!response.ok) {
        throw new Error('Failed to load conversation');
      }

      const data = await response.json();
      
      if (data.success && data.conversation) {
        // Parse messages from conversation
        const loadedMessages: Message[] = data.conversation.messages || [];
        setMessages(loadedMessages);
        setConversationId(convId);
      }
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Failed to load conversation';
      setError(errorMessage);
      onError?.(err instanceof Error ? err : new Error(errorMessage));
    } finally {
      setIsLoading(false);
    }
  }, [onError]);

  // Send a new message
  const sendMessage = useCallback(async (content: string) => {
    if (!content.trim() || isLoading) return;

    const userMessage: Message = {
      id: `msg_${Date.now()}_user`,
      role: 'user',
      content: content.trim(),
      timestamp: new Date(),
    };

    // Add user message immediately
    setMessages((prev) => [...prev, userMessage]);
    setIsLoading(true);
    setError(null);

    try {
      const response = await fetch('/api/v1/ai/chat', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          message: content.trim(),
          subAccountId,
          userId,
          conversationId,
        }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Failed to send message');
      }

      const data = await response.json();

      if (data.success) {
        // Add AI response
        const aiMessage: Message = {
          id: data.data?.message?.id || `msg_${Date.now()}_ai`,
          role: 'assistant',
          content: data.data?.message?.content || data.response || 'I processed your request.',
          timestamp: new Date(data.data?.message?.createdAt || Date.now()),
          functionCalls: data.functionCalls?.map((fc: any) => ({
            name: fc.name,
            arguments: fc.args,
            result: fc.response,
            status: 'success' as const,
          })),
        };

        setMessages((prev) => [...prev, aiMessage]);

        // Update conversation ID if this was the first message
        if (data.data?.conversationId && !conversationId) {
          setConversationId(data.data.conversationId);
        }
      } else {
        throw new Error(data.error || 'Failed to get AI response');
      }
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Failed to send message';
      setError(errorMessage);
      onError?.(err instanceof Error ? err : new Error(errorMessage));

      // Add error message to chat
      const errorMsg: Message = {
        id: `msg_${Date.now()}_error`,
        role: 'assistant',
        content: `Sorry, I encountered an error: ${errorMessage}`,
        timestamp: new Date(),
      };
      setMessages((prev) => [...prev, errorMsg]);
    } finally {
      setIsLoading(false);
    }
  }, [subAccountId, userId, conversationId, isLoading, onError]);

  // Clear all messages
  const clearMessages = useCallback(() => {
    setMessages([]);
    setConversationId(undefined);
    setError(null);
  }, []);

  return {
    messages,
    isLoading,
    error,
    sendMessage,
    clearMessages,
    loadConversation,
  };
}
