'use client'

// ==========================================
// RISIVO CRM - Dynamic Favicon Component
// ==========================================
// Updates the browser tab favicon based on sub-account theme
// Supports PNG, SVG, and ICO formats
// Uses safe DOM manipulation to avoid React reconciliation errors

import { useEffect, useRef } from 'react'

// Unique ID for our managed favicon link
const FAVICON_ID = 'risivo-dynamic-favicon'

interface DynamicFaviconProps {
  faviconUrl?: string | null
  fallbackUrl?: string
}

/**
 * Safely update or create favicon link in head
 * This function is safe to call during navigation
 */
function updateFaviconLink(iconUrl: string) {
  if (typeof window === 'undefined') return
  
  // Determine the mime type based on the URL
  const getMimeType = (url: string): string => {
    if (url.startsWith('data:')) {
      const match = url.match(/^data:([^;]+);/)
      return match ? match[1] : 'image/png'
    }
    if (url.endsWith('.svg')) return 'image/svg+xml'
    if (url.endsWith('.ico')) return 'image/x-icon'
    return 'image/png'
  }
  
  const mimeType = getMimeType(iconUrl)
  
  // For data URLs, use directly; for regular URLs, add cache buster
  const finalUrl = iconUrl.startsWith('data:') 
    ? iconUrl 
    : `${iconUrl}?v=${Date.now()}`
  
  // Try to find our existing managed link
  let existingLink = document.getElementById(FAVICON_ID) as HTMLLinkElement | null
  
  if (existingLink) {
    // Update existing link - this is safe during navigation
    existingLink.href = finalUrl
    existingLink.type = mimeType
  } else {
    // Create new favicon link with ID for future updates
    const link = document.createElement('link')
    link.id = FAVICON_ID
    link.rel = 'icon'
    link.type = mimeType
    link.href = finalUrl
    document.head.appendChild(link)
  }
}

/**
 * DynamicFavicon Component
 * 
 * Dynamically updates the browser's favicon based on the sub-account theme.
 * This enables true white-label branding where each sub-account can have
 * their own browser tab icon.
 * 
 * IMPORTANT: Uses safe DOM manipulation to avoid React reconciliation errors
 * during client-side navigation. Instead of removing and recreating links,
 * we update existing links in place using a unique ID.
 * 
 * Usage:
 * <DynamicFavicon faviconUrl={theme.faviconUrl} />
 */
export function DynamicFavicon({ 
  faviconUrl, 
  fallbackUrl = '/risivo-favicon.png' 
}: DynamicFaviconProps) {
  
  const previousUrl = useRef<string | null>(null)
  
  useEffect(() => {
    const iconUrl = faviconUrl || fallbackUrl
    
    // Skip if URL hasn't changed
    if (previousUrl.current === iconUrl) return
    previousUrl.current = iconUrl
    
    // Safely update favicon
    updateFaviconLink(iconUrl)
    
  }, [faviconUrl, fallbackUrl])
  
  // This component doesn't render anything visible
  return null
}

/**
 * Hook for programmatically updating favicon with cache busting
 * Useful when you need to update favicon outside of component lifecycle
 */
export function useDynamicFavicon() {
  const setFavicon = (url: string) => {
    updateFaviconLink(url)
  }
  
  const resetFavicon = (defaultUrl = '/risivo-favicon.png') => {
    updateFaviconLink(defaultUrl)
  }
  
  return { setFavicon, resetFavicon }
}

export default DynamicFavicon
